@keyframes fadein {
    from {
        opacity:0;
    }
    to{
        opacity:1;
    }
}
@-moz-keyframes fadein { /* Firefox */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-o-keyframes fadein { /* Opera */
    from {
        opacity:0;
    }
    to {
        opacity: 1;
    }
}
.coreMain{
    animation: fadein 0.3s;
    -moz-animation: fadein 0.2s; /* Firefox */
    -webkit-animation: fadein 0.2s; /* Safari and Chrome */
    -o-animation: fadein 0.2s; /* Opera */
    color : #ca9372;
    height:100%;
    width:100%;
    padding-top:7%;
}
.coreRow{
    display : flex;
    flex-direction: row;
    justify-content: center;
}
.coreImg {
    width:11%;
    margin : 1%;
    border-radius: 7px;
    position: relative;
}
.coreImg img{
    width:100%;
    border-radius: 7px;
}
.itemImg:hover{
    opacity:0.3;
}
.coreImg span
{
  display:none;
  -moz-border-radius:6px;
  -webkit-border-radius:6px;
  border-radius:6px;
  color:#cecece;
  background:#111111; 
  text-align: center;
}
 
.coreImg span img
{
  width:30%;
  float:center;
  margin:0px 8px 8px 0;
  border-radius: 7px;
}
 
.coreImg:hover span
{
  font-size:18px;
  display:block;
  position:absolute;
  top:0;
  left:0;
  z-index:1000;
  width:150px;
  height:auto;
  border:none;
  margin-top:100px;
  margin-left:-30px;
  overflow:hidden;
  padding:3px;
  align-content: center;
  opacity: 0.9;
}
@media (max-width:800px){
    .coreImg{
        width:13%;
    }
    .coreImg:hover span{
        margin-top:60px;
        font-size: 14px;
        width:120px;
    }
  }